.app {
  background-color: #252d4a;
  width: 500px;
  min-height: 250px;
  height: min-content;
  border-radius: 15px;
  padding: 20px;
  box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
  display: flex;
  flex-direction: column;
  gap: 5px;
  justify-content: space-evenly;
  color: white;
}

@media (max-width: 768px) {
  .app {
    width: 80%; /* Adjust width for smaller screens */
    flex-direction: column;
    align-items: center;
    margin: auto;
  }
.answer-section button{
    margin: 5px;
}
}

.score-section {
  text-align: center;
}

.question-section {
  width: 100%;
  position: relative;
}

.question-count {
  margin-bottom: 20px;
}

.question-count span {
  font-size: 28px;
}

.question-text {
  margin-bottom: 12px;
}

/* ANSWERS/RIGHT SECTION */
.answer-section {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

button {
  width: 100%;
  font-size: 16px;
  color: #ffffff;
  background-color: #252d4a;
  border-radius: 15px;
  display: flex;
  padding: 5px;
  justify-content: flex-start;
  align-items: center;
  border: 5px solid #234668;
  cursor: pointer;
}

.correct {
  background-color: #2f922f;
}


button:focus {
  outline: none;
}

button svg {
  margin-right: 5px;
}
.actions {
  display: flex;
  margin-top: 15px;
}
.actions button {
  background-color: #7cc6fe;
  justify-content: center;
}
.actions button:hover {
  background-color: #234668;
  color: white;
}

.selected {
  background-color: #2f922f;
}